import { FormControlLabel, FormGroup, Stack, Switch, Typography } from '@mui/material'
import { MailingApi } from 'API/mailing'
import DialogConfirm from 'components/DialogConfirm'
import { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'

export default function DialogDelete() {
	const nav = useNavigate()
	const { id } = useParams<{ id: string }>()
	const [loading, setLoading] = useState(false)
	const [open, setOpen] = useState(true)
	const [checked, setChecked] = useState(false)
	const dispatch = useAppDispatch()
	useEffect(() => {
		return () => {
			setOpen(false)
		}
	}, [])
	return (
		<DialogConfirm
			open={open}
			loading={loading}
			handleClose={() => {
				nav('/mailing/' + id)
			}}
			title="メールグループ削除確認"
			primaryButtonColor="error"
			primaryButtonText="削除する"
			disabled={!checked}
			handleSubmit={async () => {
				try {
					if (!id) return
					setLoading(true)
					const { message, error } = await MailingApi.delete(Number(id), true)
					dispatch(
						UIAction.addGlobalMessage({
							message,
							type: error ? 'error' : 'success',
						})
					)
					if (!error) nav('/mailing/')
				} finally {
					setLoading(false)
				}
			}}
		>
			<Stack alignItems="center" gap={2}>
				<Typography>グループのメーリングリストも含めて、削除するのでよろしいですか</Typography>
				<FormGroup>
					<FormControlLabel
						control={<Switch checked={checked} onChange={(e) => setChecked(e.target.checked)} />}
						label="同意する"
					/>
				</FormGroup>
			</Stack>
		</DialogConfirm>
	)
}
